import {onSnapshot} from "mobx-state-tree"
import {RootStore, RootStoreModel} from "./root-store"
import {Environment} from "../environment"
import * as storage from "../../utils/storage"
import * as Sentry from "@sentry/browser"


/**
 * The key we'll be saving our state as within async storage.
 */
const ROOT_STATE_STORAGE_KEY = "root"

/**
 * Setup the environment that all the models will be sharing.
 *
 * The environment includes other functions that will be picked from some
 * of the models that get created later. This is how we loosly couple things
 * like events between models.
 */
export async function createEnvironment() {
  const env = new Environment()
  await env.setup()
  return env
}

/**
 * Setup the root state.
 */
export async function setupRootStore() {
  let rootStore: RootStore
  let data: any

  // prepare the environment that will be associated with the RootStore.
  const env = await createEnvironment()
  try {
    // load data from storage
    data = (await storage.load(ROOT_STATE_STORAGE_KEY)) || {}
    rootStore = RootStoreModel.create(data, env)
    rootStore.loginStore.setApiToken(rootStore.loginStore.token)
    if (process.env.NODE_ENV !== 'development' && rootStore.loginStore.token) {
      Sentry.setUser({
        id: rootStore.loginStore.id + '',
        username: rootStore.loginStore.username as any,
        email: rootStore.loginStore.email as any
      })
    }
  } catch (e: any) {
    // if there's any problems loading, then let's at least fallback to an empty state
    // instead of crashing.
    rootStore = RootStoreModel.create({}, env)

    // but please inform us what happened
    console.log(e.message)
  }
  // Chequear si tiene permisos de ubicación
  const hasPermission = await rootStore.environment.locationService.checkGPSPermission()
  rootStore.setHasLocationPermission(hasPermission)

  // track changes & save to storage
  onSnapshot(rootStore, (snapshot: any) => storage.save(ROOT_STATE_STORAGE_KEY, snapshot))

  return rootStore
}
